.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: center; 
    align-items: center;
    padding: 0 50px;
    z-index: 1000;
    background: transparent;
}

.header.scrolled {
    background: rgba(0, 0, 0, 0.8);
    transition: background 0.3s ease-in-out;
}



.logo-container {
    position: absolute;
    top: 15px;
    left: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.logo {
    font-size: 2rem;
    font-weight: bold;
    text-decoration: none;
    color: white;
    text-align: left;
    margin-bottom: 5px; 
}

.theme-toggle {
    cursor: pointer;
    font-size: 1.4rem;
    transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.theme-toggle.light {
    color: #FFD700; 
}

.theme-toggle.dark {
    color: #87CEFA; /* Bleu clair pour la lune */
}

.theme-toggle:active {
    transform: scale(1.2); /* Effet au clic */
}

/* Navbar toujours centr√©e */
.nav-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.nav-menu li {
    list-style: none;
}

.nav-menu li a {
    font-size: 1.4rem;
    font-weight: 500;
    text-decoration: none;
    color: white;
    transition: color 0.3s ease-in-out;
}

.nav-menu li a:hover,
.nav-menu li a.active {
    color: #00aaff;
    font-weight: bold;
    border-bottom: 2px solid #00aaff;
}


.hamburger {
    display: none;
    cursor: pointer;
    z-index: 1010;
    color: white;
    transition: color 0.3s ease-in-out;
}

/* Exemple pour les ic√¥nes */
.theme-toggle {
    cursor: pointer;
    transition: color 0.3s ease;
}

.theme-toggle.light {
    color: #000; /* Ic√¥ne de th√®me clair */
}

.theme-toggle.dark {
    color: #87CEFA; /* Ic√¥ne de th√®me sombre */
}

body.light {
    background-color:#87CEFA ;
    color: #fff;
}


body.dark {
    background-color: #121212;
    color: #87CEFA;
}



/* ‚úÖ Responsive */
@media screen and (max-width: 1040px) {
    .header {
        height: 75px;
        padding: 0 20px;
    }

    .logo {
        font-size: 1.6rem;
    }

    /* En responsive : Portfolio + Ic√¥ne align√©s */
    .logo-container {
        left: 20px;
        top: 10px;
        align-items: flex-start;
    }

    .theme-toggle {
        font-size: 1.2rem;
    }

    /* Menu burger */
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: fixed;
        top: 0;
        left: -100vw;
        transition: left 0.3s ease-in-out;
        z-index: 1001;
        overflow-y: auto;
        padding: 2rem 0;
    }

    .nav-menu.active {
        left: 0;
    }

    .hamburger {
        display: block;
        position: fixed;
        top: 15px;
        right: 15px;
    }
}
